import * as React from "react";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import App from "./App";

interface Props {
  handleClick: (e: Pants) => void;
  max: number;
  current: number;
}

interface PropsState {
  max: number;
  current: number;
}

class Settings extends React.Component<Props, PropsState> {
  constructor(props: any) {
    super(props);
    this.handleChange1 = this.handleChange1.bind(this);
    this.handleChange2 = this.handleChange2.bind(this);
    this.handleClick = this.handleClick.bind(this);

    this.state = {
      max: this.props.max,
      current: this.props.current
    };
  }

  handleChange1(event: React.ChangeEvent<HTMLSelectElement>) {
    this.setState({ max: parseInt(event.target.value) });
  }

  handleChange2(event: React.ChangeEvent<HTMLSelectElement>) {
    this.setState({ current: parseInt(event.target.value) });
  }

  handleClick() {
    this.props.handleClick({
      max: this.state.max,
      current: this.state.current
    });
  }

  render() {
    return (
      <>
      
      
      <div className="inner">
        <img src="images/step1.png" className="stepimg"/>
        <p></p>
        <ul className="pants-selectbox">
          <li>
          <img src="images/pants-icon-max.png"/>
          <div className="cp_ipselect cp_sl01">
            <select value={this.state.max} onChange={this.handleChange1}>
              <option value="" hidden></option>
              <option value="0">0</option>
              <option value="1">1</option>
              <option value="2">2</option>
              <option value="3">3</option>
              <option value="4">4</option>
              <option value="5">5</option>
              <option value="6">6</option>
              <option value="7">7</option>
              <option value="8">8</option>
              <option value="9">9</option>
              <option value="10">10</option>
            </select>
          </div>
        </li>
        <li>
          <img src="images/pants-icon-clean.png"/>
          <div className="cp_ipselect cp_sl01">
            <select value={this.state.current} onChange={this.handleChange2}>
              <option value="" hidden></option>
              <option value="0">0</option>
              <option value="1">1</option>
              <option value="2">2</option>
              <option value="3">3</option>
              <option value="4">4</option>
              <option value="5">5</option>
              <option value="6">6</option>
              <option value="7">7</option>
              <option value="8">8</option>
              <option value="9">9</option>
              <option value="10">10</option>
            </select>
          </div>
        </li>
      </ul>
      <img src="images/step2.png" className="stepimg"/>
      <p></p>
      <div className="cp_ipselect2 cp_sl02">
        <select required>
          <option value="" hidden></option>
          <option value="adachi"></option>
          <option value="arakawa"></option>
          <option value="itabashi"></option>
          <option value="edogawa"></option>
          <option value="oota"></option>
          <option value="kasai"></option>
          <option value="kita"></option>
          <option value="etou"></option>
          <option value="sinagawa"></option>
          <option value="shibuya"></option>
          <option value="shinjyuku"></option>
          <option value="suginami"></option>
          <option value="sumida"></option>
          <option value="setagaya"></option>
          <option value="daitou"></option>
          <option value="tyuuou"></option>
          <option value="chiyoda"></option>
          <option value="toyosima"></option>
          <option value="nakano"></option>
          <option value="nerima"></option>
          <option value="bunkyo"></option>
          <option value="minato"></option>
          <option value="meguro"></option>
        </select>
      </div>
      <div className="here"><img src="images/nowplace.png" className="nowplace"/>&nbsp;</div>
      </div>
      
    <Link to="/">
      <div className="next"><span onClick={this.handleClick} className="ok">OK</span></div>
    </Link>
      
        <Route path="/" compknent={App} />
      </>
    );
  }
}

export default Settings;
